import React, { useEffect, useState } from "react";
import axios from "axios";
import cloneDeep from "lodash/cloneDeep";
import Pagination from 'rc-pagination';
import Select from 'react-select';
import "rc-pagination/assets/index.css";
 
function App() {
  const [isLoading, setLoading] = useState(true);
  const [publications, setPublications] = useState();
  const [publicationsNumber, setPublicationsNumber] = useState();
  const [publicationsType, setPublicationsType] = useState('All');
  const [publicationsCenter, setPublicationsCenter] = useState('All');
  const [countPerPage, setCountPerPage] = useState(10);
  const pageSizeOptions = ['10', '20', '50', '100'];
  const [currentPage, setCurrentPage] = useState(1);
  const [page, setPage] = useState();
  
  const updatePage = (pageNumber, count, type, center) => {
    setCurrentPage(pageNumber);
    setCountPerPage(count);
    setPublicationsType(type);
    setPublicationsCenter(center);
    const filterPublications = publication => {
      if(type === 'All' && center !== 'All') {
        return publication.field_abbreviated_title_url === 'ctd2' && publication.field_center === center;
      }
      if(type !== 'All' && center === 'All') {
        return publication.field_abbreviated_title_url === 'ctd2' && publication.publication_type === type;
      }
      if(type === 'All' && center === 'All') {
        return publication.field_abbreviated_title_url === 'ctd2';
      }
      if(type !== 'All' && center !== 'All') {
        return publication.field_center === center && publication.publication_type === type;
      }
    };
    setPublicationsNumber(publications.filter(filterPublications).length);
    const to = count * pageNumber;
    const from = to - count;
    setPage(cloneDeep(publications.filter(filterPublications).slice(from, to)));
  };
  
  const publicationType = [
    { value: 'All', label: 'All' },
    { value: 'Collaboration', label: 'Collaboration' },
    { value: 'Primary', label: 'Primary' },
    { value: 'Review', label: 'Review' },
    { value: 'Book Chapter', label: 'Book Chapter' },
  ];

  const center = [
    { value: 'All', label: 'All' },
    { value: 'Broad Institute', label: 'Broad Institute' },
    { value: 'Cold Spring Harbor Laboratory', label: 'Cold Spring Harbor Laboratory' },
    { value: 'Columbia University', label: 'Columbia University' },
    { value: 'Dana-Farber Cancer Institute', label: 'Dana-Farber Cancer Institute' },
    { value: 'Emory University', label: 'Emory University' },
    { value: 'Fred Hutchinson Cancer Research Center (1)', label: 'Fred Hutchinson Cancer Research Center (1)' },
    { value: 'Fred Hutchinson Cancer Research Center (2)', label: 'Fred Hutchinson Cancer Research Center (2)' },
    { value: 'Johns Hopkins University', label: 'Johns Hopkins University' },
    { value: 'Oregon Health and Science University (1)', label: 'Oregon Health and Science University (1)' },
    { value: 'Oregon Health and Science University (2)', label: 'Oregon Health and Science University (2)' },
    { value: 'Stanford University', label: 'Stanford University' },
    { value: 'Translational Genomics Research Institute', label: 'Translational Genomics Research Institute' },
    { value: 'University of California San Diego', label: 'University of California San Diego' },
    { value: 'University of California San Francisco (1)', label: 'University of California San Francisco (1)' },
    { value: 'University of California San Francisco (2)', label: 'University of California San Francisco (2)' },
    { value: 'University of Texas MD Anderson Cancer Center', label: 'University of Texas MD Anderson Cancer Center' },
    { value: 'University of Texas Southwestern Medical Center', label: 'University of Texas Southwestern Medical Center' },
  ];
  
  const pageSize = [
    { value: 10, label: '10' },
    { value: 25, label: '25' },
    { value: 50, label: '50' },
  ];
  
  const baseurl = "https://ocg.cancer.gov/";
  
  useEffect(() => {
    axios.get(`${baseurl}publications-export`).then((response) => {
      setPublications(response.data);
      setPage(cloneDeep(response.data.slice(0, countPerPage)));
      setLoading(false);
    });
  }, []);
    
  
  if (isLoading) {
    return (
      <div
        style={{
          display: "flex",
          flexDirection: "column"
        }}
      >
        Loading...
      </div>
    );
  }
    return (
      <div
        style={{
          display: "flex",
          flexDirection: "column",
        }}
      >
      <div
        style={{
          display: "flex",
          flexDirection: "row",
          'justify-content': "space-evenly",
        }}
      >
      <div>
        <Select
          options={publicationType}
          onChange={(e) => updatePage(1, countPerPage, e.value, publicationsCenter)}
          defaultValue={publicationsType}
          aria-label="Type of Publication"
        />
      </div>
      <div>
        <Select
          options={center}
          onChange={(e) => updatePage(1, countPerPage, publicationsType, e.value)}
          defaultValue={publicationsCenter}
          aria-label="Center"
        ></Select>
      </div>
      <div>
        <Select
          options={pageSize}
          onChange={(e) => updatePage(1, e.value, publicationsType, publicationsCenter)}
          defaultValue={countPerPage}
          aria-label="Items per page"
        ></Select>
      </div>
      </div>
      {page.map(publication => {
        return (
          <p key={publication.nid}>
          <span>{publication.title}</span><br />
            <a href={publication.field_publication_journal}>{publication.field_publication_citation}</a>
          </p>
        );
      })}
        <div>
          <Pagination
            pageSize={countPerPage}
            onChange={(e) => updatePage(e, countPerPage, publicationsType, publicationsCenter)}
            current={currentPage}
            total={publicationsNumber ? publicationsNumber : publications.length}
            locale={"en-us"}
            showSizeChanger
            totalBoundaryShowSizeChanger="50"
            pageSizeOptions={pageSizeOptions}
          />    
        </div>
      </div>
    );
    
          
}
  
export default App;